button {
  width: 200px;
  height: 60px;
  align-items: center;
  position: relative;
}

.button-position {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
}

.button-text {
  font-size: 30px;
  font-weight: 500;
}

